.ResultsList {
  min-width: 70vw;
  font-family: 'Roboto',sans-serif;
  margin: 30px;
}

table {
  width: 90vw;
  margin: 50px auto 0px auto;
  border-collapse: collapse;
}

table, th {
  border: 2px solid #61666d;
}

th {
  background-color: #bbb;
  text-align: center;
}

.sort-arrow  {
  display: inline;
  margin-left: 16px;
}
